<template>
  <!-- 汙水處理廠頁面 -->
  <div class="dirtywater">
    <!-- 標題 -->
    <span
      class="badge badge-pill badge-info animate__animated animate__fadeInUp text-center title"
      style="animation-duration: 1s; animation-iteration-count: 1"
      >汙水處理廠</span
    >
    <div class="subtitle">廠址資訊</div>
    <div
      class="row justify-content-center align-items-center"
      style="margin: 2% 0"
    >
      <div class="col-xl-5 col-12 text-center">
        <!-- 圖標說明 -->
        <el-card class="iconExplainCard">
          <el-row type="flex" align="middle">
            <el-col :span="12" class="iconExplainCard_title">
              圖標
              <br />說明
            </el-col>
            <el-col :span="12">
              <div
                v-for="(item, index) in iconimg"
                :key="item"
                class="iconExplain"
              >
                <img :src="item" class="iconExplain_img" />
                <span>{{ iconExplain[index] }}</span>
              </div>
            </el-col>
          </el-row>
        </el-card>
        <!-- 地圖 -->
        <l-map
          class="map"
          :zoom="zoom"
          :options="options"
          :center="center"
          v-if="dirtywaterData.length !== 0"
        >
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="(item, index) in MapData"
            :key="item.name"
          >
            <l-icon :icon-url="iconColor(index)" />
            <!-- 地標圖示 -->
            <l-tooltip :options="tooltipOptions" @ready="ready(item)">
              <div class="tooltip-content">
                <div>{{ tooltipName[index] }}</div>
              </div>
            </l-tooltip>
            <!-- 點擊圖標會彈出顯示的文字 -->
            <l-popup>{{ item.name }}</l-popup>
          </l-marker>
        </l-map>
      </div>
      <!-- 各汙水處理廠資訊 -->
      <div class="col-xl-7 col-12 text-center" style="height: 516px">
        <table class="table table-striped" style="height: 100%">
          <thead>
            <tr>
              <th scope="col"></th>
              <th scope="col">廠名</th>
              <th scope="col">平均噸數</th>
              <th scope="col">處理等級</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in dirtywaterData" :key="item.廠區">
              <th scope="row">{{ index + 1 }}</th>
              <td>{{ dirtywaterData[index].廠區 }}</td>
              <td>{{ dirtywaterData[index].平均噸數 }}</td>
              <td>{{ dirtywaterData[index].處理等級 }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- 名詞解釋區 -->
    <hr class="hr" />
    <div class="subtitle">名詞解釋</div>
    <!-- 汙水處理等級區 -->
    <div class="highlightWord">甚麼是處理等級</div>
    <div class="row justify-content-center align-items-center">
      <!-- 左方汙水處理階級按鈕說明 -->
      <div class="col-xl-8 col-12">
        <el-row :gutter="20">
          <el-col :xs="24" :span="8">
            <el-card class="firstCard">
              <div slot="header" class="clearfix">
                <span>第一級 ( 初級 ) 處理</span>
              </div>
              <div>
                去除污水中呈懸浮狀況的
                <b style="color: red">固體污染物質</b
                >，物理處理法大部分只能完結一級處理的要求。
                <br />通過一級處理的污水，生化需氧量 (BOD)
                一般可去除30%左右，但仍達不到排放規範。
              </div>
            </el-card>
          </el-col>
          <el-col :xs="24" :span="8">
            <el-card class="secondCard">
              <div slot="header" class="clearfix">
                <span>第二級處理</span>
              </div>
              <div>
                去除污水中呈膠體和溶解狀況的
                <span style="color: red; font-weight: 600">有機污染物質。</span>
                <br />1. 生化需氧量( BOD ) <br />2. 化學需氧量( COD
                )，去除率可達90%以上，使有機污染物到達排放規範。
              </div>
            </el-card>
          </el-col>
          <el-col :xs="24" :span="8">
            <el-card class="thirdCard">
              <div slot="header" class="clearfix">
                <span>第三級處理</span>
              </div>
              <div>
                進一步處理難降解的有機物、氮和磷等，可以導致水體富營養化的
                <b style="color: red">可溶性無機物</b>等。 <br /><b
                  >首要辦法有：</b
                >
                <br />生物脫氮除磷法、混凝沉澱法、砂濾法、活性炭吸附法、離子交換法，和電滲分析法等。
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <!-- 右方汙水處理階層圖片 -->
      <div class="col-xl-4 col-12 text-center">
        <div class="col-md-4 col-xl-12">
          <img
            src="@/assets/img/汙水處理階層.png"
            class="card-img one"
            alt="..."
          />
        </div>
      </div>
    </div>
    <!-- 汙水下水道區 -->
    <div class="highlightWord">要怎麼淨化汙水</div>
    <div class="row justify-content-center align-items-center">
      <!-- 左方對話框 -->
      <div class="col-xl-5 col-12 window" style="padding: 0 0 0 5%">
        <div aria-live="polite" aria-atomic="true" class="window1">
          <div style="position: absolute; top: 0; left: 0">
            <div
              class="toast"
              id="toast1"
              data-autohide="false"
              role="alert"
              aria-live="assertive"
              aria-atomic="true"
            >
              <div class="toast-header">
                <img
                  src="@/assets/img/waterperson.png"
                  class="rounded mr-2"
                  alt="..."
                />
                <strong class="mr-auto">水精靈</strong>
                <small class="text-muted">今天</small>
                <button
                  type="button"
                  class="ml-2 mb-1 close"
                  data-dismiss="toast"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="toast-body">你們家接汙水管道了嗎 ?</div>
            </div>
          </div>

          <div style="position: absolute; top: 100px; left: 120px">
            <div
              class="toast"
              id="toast2"
              data-autohide="false"
              role="alert"
              aria-live="assertive"
              aria-atomic="true"
            >
              <div class="toast-header">
                <img
                  src="@/assets/img/userpic.png"
                  class="rounded mr-2"
                  alt="..."
                />
                <strong class="mr-auto">用戶A</strong>
                <small class="text-muted">今天</small>
                <button
                  type="button"
                  class="ml-2 mb-1 close"
                  data-dismiss="toast"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="toast-body">那是甚麼啊 ? 為甚麼要接 ?</div>
            </div>
          </div>

          <div style="position: absolute; top: 200px; left: 120px">
            <div
              class="toast"
              id="toast3"
              data-autohide="false"
              role="alert"
              aria-live="assertive"
              aria-atomic="true"
            >
              <div class="toast-header">
                <img
                  src="@/assets/img/userpic.png"
                  class="rounded mr-2"
                  alt="..."
                />
                <strong class="mr-auto">用戶B</strong>
                <small class="text-muted">今天</small>
                <button
                  type="button"
                  class="ml-2 mb-1 close"
                  data-dismiss="toast"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="toast-body">你還不知道啊 汙水管道可是很重要的</div>
            </div>
          </div>

          <div style="position: absolute; top: 320px; left: 0">
            <div
              class="toast"
              id="toast4"
              data-autohide="false"
              role="alert"
              aria-live="assertive"
              aria-atomic="true"
            >
              <div class="toast-header">
                <img
                  src="@/assets/img/waterperson.png"
                  class="rounded mr-2"
                  alt="..."
                />
                <strong class="mr-auto">水精靈</strong>
                <small class="text-muted">今天</small>
                <button
                  type="button"
                  class="ml-2 mb-1 close"
                  data-dismiss="toast"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="toast-body">
                真的很重要呢 快看看右方的說明了解一下吧
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 右方汙水下水道問答區 -->
      <div class="col-xl-7 col-12 question">
        <div class="dwinfor1">
          <div class="dwinforTitle">Q： 什麼是污水下水道？</div>
          A：
          污水下水道將生活污水利用管線收集至污水處理廠處理，以改善生活環境品質
        </div>
        <div class="dwinfor2">
          <div class="dwinforTitle">Q： 污水下水道又分很多種？</div>
          A：污水下水道依功能分為 : <br />1. 公共污水下水道 =>
          埋設於道路面下供公眾使用，其由水利資源處負責管理 <br />2.
          專用污水下水道 => 供特定場所使用，其由該開發單位或其上級主管機關負責
          <br />3. 建築物污水處理設施 => 用以處理建築物的廁所排水及生活雜排水等
          <br />
        </div>
        <div style="text-align: right">
          ※ 點擊連結看看住家附近有沒有接管吧！<br />
          <a href="https://sewergis.kcg.gov.tw/">高雄汙水下水道接管查詢</a>
        </div>
      </div>
    </div>
    <hr class="hr" />
    <div class="subtitle">相關資料查詢</div>
    <div class="col-xl-12">
      <div class="row">
        <div class="col-xl-5 col-12">
          <div class="row text-center" style="margin-bottom: 3%">
            <div class="col-xl-5 col-4"></div>
            <div class="col-xl-4 col-4 searchTitle">
              同年各月資料
              <br />(單位:月)
            </div>
            <div class="col-xl-3 col-4 searchTitle">
              多年比較資料
              <br />(單位:年)
            </div>
          </div>
          <div
            class="row text-center justify-content-center align-items-center"
            v-for="(item, index) in searchType"
            :key="item.name"
          >
            <div class="col-xl-5 col-4">
              <div
                class="alert alert-primary"
                role="alert"
                style="background-color: #3f84ad; font-weight: bold"
              >
                {{ item.name }}
              </div>
            </div>
            <div class="col-xl-4 col-4">
              <el-select
                v-model="chartyear[index]"
                placeholder="年分"
                @change="
                  searchChart(item.columnName, item.name, index, 'month')
                "
              >
                <el-option
                  v-for="item in chartyearArray"
                  :key="item"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
            </div>
            <div class="col-xl-3 col-4">
              <button
                type="button"
                class="btn color"
                id="publicyear"
                @click="searchChart(item.columnName, item.name, index, 'year')"
              >
                查詢
              </button>
            </div>
          </div>
        </div>
        <!-- 圖表 -->
        <div class="col-xl-7 col-12 text-center" id="chartreport">
          <chart
            v-if="ChartOption.xAxis.data.length"
            height="450"
            style="width: 100%; height: 450px; margin-top: 30px"
          ></chart>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import echarts from "echarts";
import $ from "jquery";
axios.defaults.headers.common["Content-Type"] =
  "application/x-www-form-urlencoded";
axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*";
export default {
  mounted() {
    this.dirtywater();
    this.dialogAnimation();
    this.chart = echarts.init(document.getElementById("chartreport"));
    this.chart.setOption(this.ChartOption);
    this.searchChart("mix", "綜合圖表", 4, "year");
  },
  data() {
    return {
      chart: null,
      searchType: [
        { name: "公共污水下水道普及率", columnName: "public" },
        { name: "專用污水下水道普及率", columnName: "special" },
        { name: "建築物污水設施設置率", columnName: "building" },
        { name: "整體污水處理率合計", columnName: "total" },
        { name: "綜合圖表", columnName: "mix" },
      ],
      chartyearArray: ["108", "107", "106", "105", "104"],
      ChartOption: {
        title: {
          text: "", //主標題
          textStyle: {
            color: "#006030",
            fontSize: 16,
            align: "center",
          },
          subtext: "", //副標題
          subtextStyle: {
            color: "#642100",
            fontSize: 14,
            align: "center",
          },
          itemGap: 7,
        },
        grid: {
          top: "20%", //距上边距
        },
        legend: {
          right: 0,
          top: 0,
          data: [],
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        xAxis: {
          type: "category",
          data: [],
        },
        yAxis: {
          type: "value",
          max: 0,
          min: 0,
        },
        series: [
          {
            name: "",
            data: [],
            label: {
              show: true,
              position: "top",
            },
            type: "line",
          },
        ],
      },
      chartyear: [],
      mixchartyear: "",
      //tooltip顯示的內容設定
      tooltipName: [],
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [0, -10],
      },
      iconExplain: ["初級處理", "生物二級處理", "三級處理"],
      // ---------------------水庫資料---------------------
      dirtywaterData: [],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 10,
      //地圖中心
      center: [22.732786, 120.342959],
      // 地標圖示
      iconimg: [
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png",
        "https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-yellow.png",
      ],
      // iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      // ---------------------地圖打點資料---------------------
      MapData: [
        {
          name: "中區污水處理廠",
          local: [22.586334, 120.287168],
        },
        {
          name: "鳳山溪污水處理廠",
          local: [22.598869, 120.351317],
        },
        {
          name: "大樹污水處理廠",
          local: [22.686997, 120.434006],
        },
        {
          name: "楠梓污水處理廠",
          local: [22.728425, 120.269288],
        },
        {
          name: "旗美污水處理廠",
          local: [22.879226, 120.496331],
        },
      ],
      ctx: "",
      animation: "",
      animation2: "",
    };
  },
  destroyed() {
    clearInterval(this.animation);
  },
  methods: {
    // 取出汙水處理廠名稱，以顯現在地圖的tooltip上
    ready(item) {
      let name = item.name.split("");
      name = name[0] + name[1];
      this.tooltipName.push(name);
    },
    // 根據汙水處理廠處理等級，更改地圖上icon顏色
    iconColor(index) {
      if (this.dirtywaterData[index].處理等級 === "初級處理") {
        return this.iconimg[0];
      } else if (this.dirtywaterData[index].處理等級 === "生物二級處理") {
        return this.iconimg[1];
      } else {
        return this.iconimg[2];
      }
    },
    // 取得汙水處理廠資料
    async dirtywater() {
      this.dirtywaterData = require("@/file/dirtywater.json");
      console.log(this.dirtywaterData);
    },
    // 對話框動畫
    dialogAnimation() {
      $(".toast").toast("show");
      this.animation = setInterval(function () {
        setTimeout(function () {
          $(".toast").toast("hide");
          $("#toast1").toast("show");
        }, 1000);
        setTimeout(function () {
          $("#toast2").toast("show");
        }, 1400);
        setTimeout(function () {
          $("#toast3").toast("show");
        }, 1800);
        setTimeout(function () {
          $("#toast4").toast("show");
        }, 2200);
        $(".toast").toast("hide");
      }, 5000);
    },
    // 資料查詢
    // --------------          傳入參數說明:          --------------
    //      dbname    ||     name   ||        index      ||   type
    //   資料庫欄位名稱 ||  中文名稱  ||   點選第幾個項目 ||   月/年查詢
    //      public || 公共污水下水道普及率 ||    0    ||       month    ---範例
    searchChart(dbname, name, index, type) {
      this.ChartOption.title.text = name; // 設置圖表title
      this.ChartOption.xAxis.data = [];
      this.ChartOption.legend.data = [];
      this.ChartOption.series = [
        {
          name: "",
          data: [],
          label: {
            show: true,
            position: "top",
          },
          type: "line",
        },
      ];
      this.ChartOption.series[0].name = name;
      var mixDetail = [
        {
          name: "公共污水下水道普及率",
          data: [],
          label: {
            show: true,
            position: "top",
          },
          type: "line",
        },
        {
          name: "專用污水下水道普及率",
          data: [],
          label: {
            show: true,
            position: "top",
          },
          type: "line",
        },
        {
          name: "建築物污水設施設置率",
          data: [],
          label: {
            show: true,
            position: "top",
          },
          type: "line",
        },
        {
          name: "整體污水處理率合計",
          data: [],
          label: {
            show: true,
            position: "top",
          },
          type: "line",
        },
      ];
      let dirtywaterDB = require("@/file/dirtywaterDB.json");
      let monthData = [];
      let yearData = [];
      switch (type) {
        case "month":
          this.ChartOption.series[0].data = [];
          monthData = dirtywaterDB.filter((item) => {
            return item.year == this.chartyear[index] && item.average == 0;
          });
          console.log(monthData);
          if (dbname !== "mix") {
            this.ChartOption.yAxis.min = parseInt(monthData[0][dbname]);
            this.ChartOption.yAxis.max = parseInt(monthData[0][dbname]);
            this.ChartOption.legend.data.push(name);
          } else {
            this.ChartOption.yAxis.min = parseInt(monthData[0]["special"]);
            this.ChartOption.yAxis.max = parseInt(monthData[0]["total"]);
            mixDetail.forEach((item) => {
              item.data = [];
              this.ChartOption.legend.data.push(item.name);
            });
          }
          monthData.forEach((item) => {
            if (this.ChartOption.yAxis.max < item[dbname]) {
              this.ChartOption.yAxis.max = parseInt(item[dbname]);
            }
            if (this.ChartOption.yAxis.min > item[dbname]) {
              this.ChartOption.yAxis.min = parseInt(item[dbname]);
            }
            this.ChartOption.title.subtext = this.chartyear[index] + "年資料"; // 設置圖表副title
            this.ChartOption.xAxis.data.push(item.date + "月");
            if (dbname !== "mix") {
              this.ChartOption.series[0].data.push(item[dbname]);
            } else {
              if (this.ChartOption.yAxis.max < item["total"]) {
                this.ChartOption.yAxis.max = parseInt(item["total"]);
              }
              if (this.ChartOption.yAxis.min > item["special"]) {
                this.ChartOption.yAxis.min = parseInt(item["special"]);
              }
              mixDetail[0].data.push(item["public"]);
              mixDetail[1].data.push(item["special"]);
              mixDetail[2].data.push(item["building"]);
              mixDetail[3].data.push(item["total"]);
            }
          });
          if (dbname === "mix") {
            this.ChartOption.series = mixDetail;
          }
          this.ChartOption.yAxis.max = this.ChartOption.yAxis.max + 5;
          this.ChartOption.yAxis.min = this.ChartOption.yAxis.min - 3;
          if (this.ChartOption.yAxis.min < 0) {
            this.ChartOption.yAxis.min = 0;
          }
          console.log(this.ChartOption.series);
          console.log(this.ChartOption.legend.data);
          this.chart.clear();
          this.chart.setOption(this.ChartOption);
          break;
        case "year":
          yearData = dirtywaterDB.filter((item) => {
            return item.average == 1;
          });
          console.log(yearData);
          if (dbname !== "mix") {
            this.ChartOption.yAxis.min = parseInt(yearData[0][dbname]);
            this.ChartOption.yAxis.max = parseInt(yearData[0][dbname]);
            this.ChartOption.legend.data.push(name);
          } else {
            this.ChartOption.yAxis.min = parseInt(yearData[0]["special"]);
            this.ChartOption.yAxis.max = parseInt(yearData[0]["total"]);
            mixDetail.forEach((item) => {
              item.data = [];
              this.ChartOption.legend.data.push(item.name);
            });
          }
          yearData.forEach((item) => {
            this.ChartOption.title.subtext = "104 - 108年資料";
            this.ChartOption.xAxis.data.push(item.year + "年");
            if (dbname !== "mix") {
              if (this.ChartOption.yAxis.max < item[dbname]) {
                this.ChartOption.yAxis.max = parseInt(item[dbname]);
              }
              if (this.ChartOption.yAxis.min > item[dbname]) {
                this.ChartOption.yAxis.min = parseInt(item[dbname]);
              }
              this.ChartOption.series[0].data.push(item[dbname]);
            } else {
              if (this.ChartOption.yAxis.max < item["total"]) {
                this.ChartOption.yAxis.max = parseInt(item["total"]);
              }
              if (this.ChartOption.yAxis.min > item["special"]) {
                this.ChartOption.yAxis.min = parseInt(item["special"]);
              }
              mixDetail[0].data.push(item["public"]);
              mixDetail[1].data.push(item["special"]);
              mixDetail[2].data.push(item["building"]);
              mixDetail[3].data.push(item["total"]);
            }
          });
          if (dbname === "mix") {
            this.ChartOption.series = mixDetail;
          }
          this.ChartOption.yAxis.max = this.ChartOption.yAxis.max + 5;
          this.ChartOption.yAxis.min = this.ChartOption.yAxis.min - 3;
          if (this.ChartOption.yAxis.min < 0) {
            this.ChartOption.yAxis.min = 0;
          }
          console.log(this.ChartOption.series);
          this.chart.clear();
          this.chart.setOption(this.ChartOption);
          break;
      }
    },
  },
};
</script>
